import Vue from 'vue'
import router from './router'
import store from './store'
import '@/common/css/reset.css'
import '@/common/css/element-variables.scss'
import axios from '@/ad/ajax.js'
import ad from './ad.vue'

import  VueJsonp  from  'vue-jsonp'

Vue.use(VueJsonp);
import { 
    Select,
    Option,
    Header,
    Container,
    Input,
    Dialog,
    DatePicker,
    Pagination,
    Tooltip,
    Button,
    Message
 } from 'element-ui';
Vue.use(Select);
Vue.use(Option);
Vue.use(Header);
Vue.use(Container);
Vue.use(Input);
Vue.use(Dialog);
Vue.use(DatePicker);
Vue.use(Pagination);
Vue.use(Tooltip);
Vue.use(Button);
Vue.prototype.$message = Message;

Vue.prototype.axios = axios;
Vue.config.productionTip = false;

axios.get(`${process.env.BASE_URL}domain.json`)
    .then(res => {
        axios.defaults.baseURL = res.data.baseUrl;
        new Vue({
            router,
            store,
            render: h => h(ad)
        }).$mount('#app')

    }).catch(err => {
    console.log(err);
});


