      <div
        style="text-indent:30px;"
      >我叫张勇，今年39岁，是一个饭店老板，因生意需要长期饮酒熬夜，生活没有规律，最终导致身体状况出现问题，长期血尿酸500umol/L左右，非常高。患病11年时间里不断接受治疗，尿酸始终没有抑制得住，双脚、双手第一跖趾关节处出现痛风石，折磨得我生不如死，感恩让我遇见王中周院长让我摆脱痛风石的折磨。</div>
      <div
        style="text-indent:30px;"
      >记忆犹新，我得病在2008年9月的一天晚上，感觉右脚发热疼痛，那种感觉就好像是滚烫的蚂蚁在骨头缝里爬似的。之后去当地医院检查，这是我第一次验尿酸是570umol/L，医生说这肯定是痛风，此后，就开始治疗痛风。我尝试过很多治病方法，吃西药、喝中药、针灸、物理疗法，但是没想到钱花了不少病还没治好。</div>
      <div
        style="text-indent:30px;"
      >2014年，我的脚上开始有硬硬的东西出现。去医院检查为痛风石，医生建议手术治疗，但考虑到手术风险大，效果不一定好，就没有接受，而是选择理疗。直到2016年，我的双脚、双手出现多处痛风石，双脚第一跖趾关节痛风石巨大，还溃破流脓，无法正常行走。这样的情况不但给我的身体带来巨大伤痛，生意也无法继续做下去了，后来经朋友推荐找到了京师中医楼的王中周院长。</div>
      <div
        style="text-indent:30px;"
      >王院长为我远程诊断，并根据我的病情为我制定了系统化的治疗方案，并服用了《肝肾痛风专方》。治疗没几天，我就感觉浑身轻松，脚趾关节疼痛也没有以前严重了。经过三个疗程治疗后，我脚上的痛风石消失，疼痛红肿也都不见了，关节活动自如，可自由行走，蹦跳也没问题。如今，时间已经过去两年了，我的痛风再也没有犯过。真的很感谢京师中医楼，感谢王中周院长的治疗，也希望更多的痛风患者能够早日治疗、早日康复。</div>
