      <div
        style="text-indent:30px;"
      >我从小家庭环境很好，爸爸妈妈特别疼爱我，每顿饭都必须有肉，所以我从小体重就超标，到现在成了不折不扣的大胖墩。在大学期间，又遇到了一帮同学知己更是经常胡吃海喝。在读大二时，有一天我发现脚部突然红肿、疼痛，有时候甚至疼得走不了路。到医院一查，我居然得了痛风。医生叮嘱我除了按时服药之外，在饮食上也要多加注意。但我那时候在大学，没收没管的，饮食、睡觉特别不规律，常常跟同学、哥们出去吃呀！喝呀！根本不注意！什么高热量、高脂肪的食物，海鲜呀什么也不忌口，导致痛风反复发作。好了伤疤忘了疼。</div>
      <div
        style="text-indent:30px;"
      >记得那次放假期间，在家又犯病了，那天爸爸碰巧出差没在家，是妈妈弱小的身体，连扶带拖的把我这个身高1米85，体重220斤的大小伙子带到了京师中医楼就诊， 看着妈妈当时心急如焚和累得不行的样子我感到非常自责。这是一个专门治痛风的医疗机构，院长王中周为我亲自诊断，开的《肝肾痛风专方》，通过三个月的系统规范化治疗，我的痛风恢复得很快，血尿酸十天一次检查很稳定，一直在360 umol/L左右，我自身也多方面注意了，到现在一直没有复发过。我想说的是，痛风现在已经越来越年轻化，大家不要仗着年轻就随意挥霍身体，因为我们身上肩负着不仅是自己的未来，还有一份对家人的责任。</div>
      <img src="../assets/image/img_32.jpg" alt />
      <div style="text-align:center;font-size:26px;">多亏了王院长</div>
      <div style="text-align:center;font-size:26px;">救了我这个60岁的绝望退休老人</div>
      <img src="../assets/image/img_35.jpg" alt />
      <div style="text-align:center;font-size:26px;color:red">王中周院长携专家团队为张国忠【远程会诊】制定规范性治疗方案</div>
      <div
        style="text-indent:30px;"
      >我叫张国忠，今年60岁，是一个处级退休老干部，本该拿着丰厚的退休金享受幸福的晚年生活，却因患有痛风十多年来一直被痛风折磨，双手指关节红肿、疼痛、皮温高、僵硬，指关节、指腹多发痛风石。有时候发作甚至持续2个星期。最近两年发病频率越来越多，每个月都发作，甚至几个月都不见好转。几乎所有关节都疼过，血尿酸一直很高，一直在480umol/L左右，这还是在每天吃西药控制的情况下，长期吃西药导致肠胃不适，肝肾功能严重受损，省内省外各大医院走了十几家，治了十几年，始终没有彻底治好，每当痛风发作时，我真的是生不如死！</div>
      <div
        style="text-indent:30px;"
      >就在我快绝望的时候，经朋友介绍了解到京师中医楼的王中周院长治痛风“很灵很灵”，我决定试一试！王中周院长根据我的病情，制定了系统的治疗方案，经过四个疗程的治疗后，我的拇指红肿症状消失、无疼痛感、活动自如，血尿酸降320umol/L，血常规、尿常规及肝肾功能均正常，痛风石也逐渐没有了，这是我在十几年求医过程中从未有的效果。2018月9月我的痛风真的痊愈了，这是我做梦都没想到的。感谢京师痛风，感谢王中周院长，给我一个正常人的晚年生活！</div>
      <img src="../assets/image/img_32.jpg" alt />
      <div style="text-align:center;font-size:26px;">后悔：长期高尿酸，被我最终忽视成了痛风！</div>
      <img src="../assets/image/img_36.jpg" alt />
      <div style="text-align:center;font-size:26px;color:red">王中周院长携专家团队为褚建国【远程会诊】制定规范性治疗方案</div>
      <div
        style="text-indent:30px;"
      >我今年40岁，就职于某国有企业，是一名中层领导。我们单位每年都会定期组织体检，这两年我的体检报告总显示尿酸值过高，平均显示为470—500μmol/L，但由于身体并未出现任何异常，加上工作比较忙便没有放在心上。</div>
      <div
        style="text-indent:30px;"
      >今年年初单位又组织体检是在一个三甲医院，显示尿酸值为550mol/L，我便引起了重视，先后在国内数家综合性三甲医院进行诊治，非但没有得到良好的治疗，反而尿酸值呈现上升的迹象。这时我的关节开始出现麻涨感，我更加焦急开始四处寻医问药，最后通过网络搜索咨询京师中医楼，并决定采用京师中医楼王中周院长的方案诊治。</div>
      <div
        style="text-indent:30px;"
      >坐落于辽宁省鞍山市的京师中医楼是一家专门治疗痛风的专业医疗机构，我了解到王中周院长在治疗痛风上的独特方式，也治愈了全国上万名患者，于是我将详细的个人身体状况及近期体检报告及舌苔照、患病部位的照片发给了京师痛风。王院长与专家团队通过【远程诊疗】的方式，根据我的病情专门配置了专方，加上“肝肾安糖浆”一同服用。</div>
      <div
        style="text-indent:30px;"
      >经过三疗程的系统化治疗后，我各关节的肿痛麻木都消失了，活动自如了，尿酸值也控制在了320mol/L左右的正常范围内，恢复得非常好。我非常感谢王中周院长，王院长的《肝肾痛风专方》简直太神奇了!</div>
      <img src="../assets/image/img_37.jpg" alt />
      <img src="../assets/image/img_38.jpg" alt style="width:400px;margin-left:25%" />
      <div
        style="text-indent:30px;color:red;font-size:24px;text-align:center"
      >↑长按图中二维码3秒识别添加老师咨询《肝肾痛风专方》↑</div>
      <img src="../assets/image/img_39.jpg" alt />
      <img src="../assets/image/img_40.jpg" alt />
      <div
        style="text-indent:30px;"
      >我行医30多年，已经治好了全国上万的痛风患者。每天都有不远千里来到京师中医楼找我看痛风的患者，很多患者都是因为前期仅仅尿酸值高，后发展成痛风、肾衰竭，甚至是尿毒症，后悔不已!我劝所有的痛风患者别再忽视痛风了，一定要规范化治疗痛风，这样痛风才不会反复发作，才不会伤及肝肾,你可以来院治疗，也可以加我们微信【远程会诊】都是一样的！</div>
