import axios from 'axios';
import path from 'path';

let pending = []; //声明一个数组用于存储每个ajax请求的取消函数和ajax标识
let cancelToken = axios.CancelToken;
let removeRepeatUrl = (ever,type) => {
    let responseUrl = ever.url + '&' + ever.method;
    for(let p in pending){
        if(pending[p].u === responseUrl) { //当当前请求在数组中存在时执行函数体
            if (type=='request') {
                pending[p].f('操作被用户取消'); //执行取消操作
            }
            pending.splice(p, 1); //把这条记录从数组中移除
        }
    }
}
const ins = axios.create();
// 请求拦截器
ins.interceptors.request.use(config => {
    if('get' === config.method){
        config.params = {
            _t: Date.parse(new Date()) / 1000,
            ...config.params
        }
    }
    // 这里拿到的url没有host信息所以需要手动拼接 或者 setTimeout也可以
    let configUrl = '';
    if(config.baseURL&&!config.url.includes('http://')) {
        configUrl = path.join(config.baseURL,config.url).replace('http:/','http://');
    } else {
        configUrl = config.url
    }
    //在一个ajax发送前执行一下取消操作
    config.cancelToken = new cancelToken((c)=>{
        removeRepeatUrl({
            url: configUrl,
            method: config.method
        },'request'); 
         // 自定义唯一标识
         pending.push({u: configUrl + '&' + config.method,f: c});
      });
    return config
}, err => {
    return Promise.reject(err,1)
})

// 响应拦截器
ins.interceptors.response.use(res => {
    removeRepeatUrl(res.config,'response');  //在一个ajax响应后再执行一下取消操作，把已经完成的请求从pending中移除
    const data = res.data;
    return res
}, err => {
    return Promise.reject(err)
})

export default ins
